.portfolio-slider {
    width: 100%;
    // max-width: 670px;

    height: 330px;
    overflow: hidden;
    position: relative;
}

.portfolio-slider .inslider {
    width: 4000px;
    position: absolute;
    left: -415px;
    z-index: 10;
}

.portfolio-slider-arrow {
    z-index: 20;
    position: absolute;
    width: 85px;
    height: 263px;
    /*background-color: rgba(255,255,255,0.6);*/
    top: 0;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.4);
    border-bottom: 3px solid #ffffff;

    &:hover {
        background-color: rgba(255, 255, 255, 0.6);
        transition: 300ms;
    }

    &__left {
        left: 0;

        &:before {
            content: "";
            position: absolute;
            display: block;
            top: 50%;
            right: 50%;
            transform: translate(50%, -50%);
            display: block;
            width: 30px;
            height: 40px;
            background-image: url('/images/arrow_portfolio_slider_left.png');
            background-color: rgba(255, 255, 255, 0.6);
            background-repeat: no-repeat;
            background-position: 10px 13px;
        }
    }

    &__right {
        right: 0;

        &:before {
            content: "";
            position: absolute;
            top: 50%;
            right: 50%;
            transform: translate(50%, -50%);
            display: block;
            width: 30px;
            height: 40px;
            background-image: url('/images/arrow_portfolio_slider_right.png');
            background-color: rgba(255, 255, 255, 0.6);
            background-repeat: no-repeat;
            background-position: 13px 13px;
        }
    }
}











.gallery-slider {
    width: 940px;
    height: 600px;
    overflow: hidden;
    position: relative;
}

.gallery-inslider {
    width: 940px;
    height: 600px;
    position: relative;
}

.gallery-slider-arrow {
    z-index: 20;
    position: absolute;
    width: 80px;
    height: 600px;
    /*background-color: rgba(255,255,255,0.6);*/
    top: 0;
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.2);
    border-bottom: 3px solid #ffffff;
}

.gallery-slider-arrow__left {
    left: 0;
}

.gallery-slider-arrow__right {
    right: 0;
}

.gallery-slider-arrow__left:before {
    content: "";
    display: block;
    top: 280px;
    height: 40px;
    width: 30px;
    position: absolute;
    background-image: url('/images/arrow_portfolio_slider_left.png');
    left: 25px;
    background-color: rgba(255, 255, 255, 0.6);
    background-repeat: no-repeat;
    background-position: 10px 13px;

}


.gallery-slider-arrow__right:before {
    background-image: url('/images/arrow_portfolio_slider_right.png');
    right: 25px;
    content: "";
    display: block;
    top: 280px;
    height: 40px;
    width: 30px;
    position: absolute;
    background-color: rgba(255, 255, 255, 0.6);
    background-repeat: no-repeat;
    background-position: 13px 13px;
}


.gallery-slider-arrow:hover {
    background-color: rgba(255, 255, 255, 0.6);
    transition: 300ms;
}

.content-bigimage-holder {
    float: left;
    width: 940px;
    position: relative;
}

.content-bigimage-annotate {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 740px;
    margin: 0 80px 0 80px;
    padding: 15px 20px 20px 20px;
    background-color: rgba(255, 255, 255, 0.6);
}

.content-bigimage-annotate p:first-of-type {
    margin-top: 0;
}

.content-bigimage-annotate p:last-of-type {
    margin-bottom: 0;
}

.content-bigimage-annotate-link {}

.portfolio-slide {
    display: block;
    float: left;
    text-decoration: none;
}

.portfolio-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.portfolio-slide-name {
    max-width: 500px;
    display: block;
    text-align: center;
    color: #414040;
    font-family: "Tahoma", "PT Sans", sans-serif;
    font-size: 12px;
    padding-top: 10px;
    border-top: 3px solid #f14214;
}

.portfolio-slide-more {
    width: 500px;
    display: block;
    text-align: center;
    font-family: "Tahoma", "PT Sans", sans-serif;
    text-decoration: underline;
    font-size: 12px;
    margin-top: 5px;
}




.portfolio-list-item {
    margin-top: 20px;
}

.portfolio-list-item-header {
    font-family: "Roboto", sans-serif;
    font-weight: lighter;
    color: #919191;
    font-size: 20px;
    margin-bottom: 20px;
}

.portfolio-anchor {
    position: relative;
    top: -100px;
    content: " ";
    display: block;
    width: 1px;
    height: 1px;
}

.portfolio-gallerey {
    overflow: hidden;
    height: 350px;
    position: relative;
    border-bottom: 3px solid #f14214;
    margin-bottom: 10px;
}

.portfolio-gallerey-in {
    width: 10000px;
    position: absolute;
    left: 0;
}

.portfolio-gallerey-bigimage {
    float: left;
}

.portfolio-miniimages {
    // max-width: 692px;
