.mobile-nav-icon {
  width: 30px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.mobile-nav-icon__middle {
  position: relative;
  width: 30px;
  height: 3px;
  background-color: #f14214;
  transition: background-color 0s linear 0.2s;
}
.mobile-nav-icon__middle::before, .mobile-nav-icon__middle::after {
  content: "";
  position: absolute;
  left: 0;
  width: 30px;
  height: 3px;
  background-color: #f14214;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}
.mobile-nav-icon__middle::before {
  top: -8px;
}
.mobile-nav-icon__middle::after {
  top: 8px;
}

.mobile-nav-icon.mobile-nav-icon--active .mobile-nav-icon__middle {
  background-color: transparent;
}
.mobile-nav-icon.mobile-nav-icon--active .mobile-nav-icon__middle::before, .mobile-nav-icon.mobile-nav-icon--active .mobile-nav-icon__middle::after {
  top: 0;
  background-color: #FFF;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}
.mobile-nav-icon.mobile-nav-icon--active .mobile-nav-icon__middle::before {
  transform: rotate(45deg);
}
.mobile-nav-icon.mobile-nav-icon--active .mobile-nav-icon__middle::after {
  transform: rotate(-45deg);
}

